html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  font-family: var(--font-rubik);
  background: var(--bg-body);
  color: var(--white);
  min-height: 100vh;
  background-image: url("../../../image/desktop_BG.svg");
  background-repeat: no-repeat;
  background-size: 100vw 100vh;
  background-attachment: local;
}

:root {
  --bg-body: rgba(29, 30, 30, 1);
  --main-yellow: rgba(247, 247, 36, 1);
  --white: rgba(255, 255, 255, 1);
  --bg-section: rgba(61, 61, 61, 0.28);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.yellow {
  color: var(--main-yellow);
}

.hero_main-link {
  font-weight: bold;
  font-size: 3rem;
  margin-left: 2.5rem;
  color: var(--main-yellow);
}

.hero_sub-link {
  font-weight: bold;
  font-size: 1.875rem;
  color: var(--white);
}

.hero-main-h1 {
  width: 59.375rem;
  font-size: 9.6875rem;
  margin-top: 2.1875rem;
}
@media (max-width: 1440px) {
  .hero-main-h1 {
    width: 58.125rem;
    font-size: 8.3125rem;
  }
}
@media (max-width: 1024px) {
  .hero-main-h1 {
    font-size: 7.1875rem;
    width: 50rem;
  }
}
@media (max-width: 375px) {
  .hero-main-h1 {
    font-size: 3rem;
  }
}

.section_Header-one {
  margin-top: 25rem;
  margin-bottom: 9.375rem;
  font-size: 4.5rem;
}

.section_left {
  margin-top: 5.625rem;
}
.section_mid {
  margin-top: 4.6875rem;
  margin-right: 9.375rem;
}
@media (max-width: 1440px) {
  .section_mid {
    margin-top: 2.375rem;
  }
}
.section_right {
  margin-top: 5.625rem;
  margin-left: 10.625rem;
}
.section_p {
  width: 25rem;
  text-align: center;
  font-size: 1.25rem;
}
.section_p2 {
  width: 25rem;
  text-align: right;
  font-size: 1.25rem;
}
.section_p3 {
  width: 21.25rem;
  text-align: left;
  font-size: 1.25rem;
}
@media (max-width: 1440px) {
  .section_p, .section_p2, .section_p3 {
    width: 18.75rem;
    font-size: 1.125rem;
  }
}

.text {
  display: inline-block;
  font-size: 1.75rem;
  width: 83rem;
  margin-top: 0;
  font-weight: 400;
}
@media (max-width: 1024px) {
  .text {
    width: 62.5rem;
    text-align: center;
  }
}

.last_h1 {
  display: inline-block;
  width: 62.5rem;
  font-size: 8rem;
  margin-left: 9.375rem;
}
@media (max-width: 1024px) {
  .last_h1 {
    font-size: 4.75rem;
    width: 37rem;
    margin-left: 6.25rem;
  }
}

.footer {
  color: var(--white);
  font-size: 1.875rem;
  margin-right: 0.625rem;
}

.hover:hover {
  color: var(--main-yellow);
}

:root {
  --font-rubik: "Rubik", sans-serif;
  --font-ruda: "Ruda", sans-serif;
}

.flex {
  display: flex;
  flex-wrap: nowrap;
}

.flex-dir-column {
  flex-direction: column;
}

.flex-dir-row {
  flex-direction: row;
}

.flex-jc-c {
  justify-content: center;
}

.space-around {
  justify-content: space-around;
}

.flex-jc-end {
  justify-content: flex-end;
}

.flex-ai-b {
  align-items: baseline;
}

.flex-ai-c {
  align-items: center;
}

.margin-bottom {
  margin-bottom: 5rem;
}

.flex-jc-sb {
  justify-content: space-between;
}

.wrapper {
  width: 92%;
  margin-top: 3.125rem;
  margin-bottom: 1.875rem;
}
@media (max-width: 1440px) {
  .wrapper {
    width: 83rem;
  }
}
@media (max-width: 1024px) {
  .wrapper {
    width: 61.25rem;
  }
}

.section_One {
  margin-top: 18.75rem;
}

.section_Two {
  margin-top: 12.5rem;
}

.last_Section {
  margin-top: 9.375rem;
}

.arrowLink {
  position: relative;
  top: -55rem;
}

.arrow {
  position: absolute;
  left: 87.5rem;
  top: 38.75rem;
  animation: rotation 5s infinite linear;
}
@media (max-width: 1440px) {
  .arrow {
    left: 63.75rem;
    top: 34.375rem;
  }
}
@media (max-width: 1024px) {
  .arrow {
    left: 40rem;
    top: 30rem;
  }
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
.vector {
  position: absolute;
  left: 82.8125rem;
  top: 31.25rem;
}
@media (max-width: 1440px) {
  .vector {
    left: 59.0625rem;
    top: 26.5625rem;
  }
}
@media (max-width: 1024px) {
  .vector {
    left: 35.3125rem;
    top: 21.875rem;
  }
}

.card {
  margin-top: 2.5rem;
}
.card_left, .card_mid, .card_right {
  position: relative;
  height: 43.75rem;
  width: 31.25rem;
  background: var(--bg-section);
}
@media (max-width: 1440px) {
  .card_left, .card_mid, .card_right {
    height: 37.5rem;
    width: 25rem;
  }
}
.card_left {
  margin-top: -3.75rem;
}
.card_mid {
  margin-top: -2.5rem;
}
.card_right {
  margin-top: -2.5rem;
}
.card_mt--left {
  margin-top: 2.5rem;
}
.card_mt--mid {
  margin-top: 1.25rem;
}
.card_mt--right {
  margin-top: 2.5rem;
}
@media (max-width: 1440px) {
  .card_mt--left, .card_mt--mid, .card_mt--right {
    max-width: 60%;
    height: auto;
  }
}

.triangle {
  position: absolute;
}
.triangle_left {
  top: 13.75rem;
  width: 115%;
  height: auto;
}
.triangle_mid {
  top: 15.5rem;
  left: -2.8125rem;
}
.triangle_right {
  width: 120%;
  height: auto;
  top: 10.625rem;
  left: -0.3125rem;
}
@media (max-width: 1440px) {
  .triangle_left {
    top: 11.875rem;
    width: 120%;
    height: auto;
  }
  .triangle_mid {
    top: 14.0625rem;
    left: -5.9375rem;
    width: 119%;
    height: auto;
  }
  .triangle_right {
    width: 127%;
    height: auto;
    top: 9.375rem;
    left: -2.1875rem;
  }
}

.button {
  position: absolute;
  left: 78.75rem;
  top: 39.0625rem;
}
@media (max-width: 1440px) {
  .button {
    left: 60rem;
  }
}
@media (max-width: 1024px) {
  .button {
    left: 43.125rem;
  }
}

.rectangle {
  position: relative;
  width: 104.0625rem;
  height: 51.875rem;
  background-color: var(--bg-section);
}
@media (max-width: 1024px) {
  .rectangle {
    width: 62.5rem;
    height: 31.25rem;
  }
}

.links {
  margin-top: 1.875rem;
}